.img-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25%;
  margin: 0 3% 0 3%;
  border: 3px solid black;
  height: 25em;
  border-radius: 40px;
  cursor: pointer;
}

.img-box img {
  transition: 0.5s;
}

.img-box:hover img {
  transform: scale(1.2);
}